import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { Router, ActivatedRoute } from '@angular/router';
import { Observable, Subject, asapScheduler, pipe, of, from, interval, merge, fromEvent } from 'rxjs';

@Component({
  selector: 'app-home-page',
  templateUrl: './home-page.component.html',
  styleUrls: ['./home-page.component.css']
})
export class HomePageComponent implements OnInit {

  url:string;
  items:String[] =[];
  nameId:string;
  item:string;
  
  constructor(private http: HttpClient,private routeService: Router,
    private route: ActivatedRoute) { }

  getNames() :Observable<String[]>{
    this.url ='/Names';
    return this.http.get<String[]>(this.url);
  }

  getDetails()
  {
    this.route.queryParams.subscribe(params => {
     console.log(params);
     this.nameId= params['nameId'];
     
     console.log(this.nameId);
                 });
   this.url ='/Name?nameId='+this.nameId;
   return this.http.get<string>(this.url);

  }

  ngOnInit() 
  {
    this.getNames().subscribe(data=>(this.items=data));
    this.getDetails().subscribe(data=>(this.item=data));
  }

}
